export default {
    data () {
        return  {
            sum:"",
           scoreList:[
               {key: '1', value: '<span style="color:red;font-size:1.2rem;margin-right:0.5rem">10元</span><span style="color:#999;font-size:1rem;">(1000积分)</span>'},
